import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import { IClickCell, ITooltipCell } from '../../../../types/table-cells';
import { CSSProperties } from '../../../../types/styles';

export interface ColorStatusCellParams extends ITooltipCell, IClickCell {
    color: string;
    dataPresent: boolean;
    containerStyles: CSSProperties;
}

interface InitParams extends ColorStatusCellParams, ICellRendererParams {}

/**
 * @category Table Cell
 * @description A cell component for displaying a color status.
 */

@Component({
    tag: 'atui-color-status-cell',
    shadow: false,
})
export class AtuiColorStatusCell implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() params: InitParams;
    @State() containerStyles: CSSProperties;
    dataPresent: boolean;

    init(params: InitParams): void {
        const { color, dataPresent, containerStyles } = params;

        this.dataPresent = dataPresent;
        this.params = params;
        this.containerStyles = {
            'background-color': color,
            'height': '100%',
            'width': '100%',
            'cursor': 'pointer',
            ...containerStyles,
        };
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: InitParams): boolean {
        this.updateCell(params.color);
        return true;
    }

    protected updateCell(newValue: any) {
        this.params.color = newValue;
    }

    render() {
        return (
            <Host class="flex h-full items-center leading-[100%]">
                <atui-tooltip
                    position="right"
                    is_visible={!!this.params.generateTooltip}
                >
                    <div
                        slot="tooltip-trigger"
                        style={this.containerStyles}
                        onClick={() => this.params.click(this.params)}
                        data-present={this.dataPresent}
                    />

                    {this.params.generateTooltip && (
                        <span slot="tooltip-content">
                            {this.params.generateTooltip(this.params)}
                        </span>
                    )}
                </atui-tooltip>
            </Host>
        );
    }
}
